import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
// 引入 mutations_type （引用同一個 key）
import * as types from './mutations_type.js'

export const login = ({ commit }) => {
  let vm = this
  FB.login(function (response) {
    vm.loginfact({ commit }, response)

  }, {
    scope: 'email, public_profile',
    return_scopes: true
  })

}
export const logout = ({ commit }) => {//登出時執行
  FB.logout(function (response) {
    commit(types.LOGOUT)
  })
}

export const loginfact = ({ commit }, response) => {
  if (response.status === 'connected') {//如果連接狀態為connected
    FB.api('/me?fields=name,id,email', function (response) {
      commit(types.SETPROFILE, response)//commit過濾後的資料
    })
  } else if (response.status === 'not_authorized') {//有資料未登入
    commit(types.NOAUTH)
  } else if (response.status === 'unknown') {//登出
    commit(types.LOGOUT)
  } else {
    commit(types.NOAUTH)
  }
}



