export const stateProfile = state => { return state.profile }

export const profilePicture = state => {
  return (state.profile.id) ? `https://graph.facebook.com/${state.profile.id}/picture?width=300` : `/static/man.gif`
}

export const loginstatus = state =>{ return state.authorized}


